const collapsibleElement = 'xml-upload-response-collapsible';
const uploadXml = {
  state: {
    response: {
      header: {},
      details: [],
      activeElement: []
    }
  },
  mutations: {
    SET_XML_RESPONSE: (state, response) => {
      if (response) {
        state.response.details = response.detail || [];
        state.response.header.fileName = response.fileName;
        state.response.header.orderNumber = response.orderNumber;
        state.response.header.shipdate = response.shipdate;
        state.response.header.shipper = response.shipper;
        state.response.header.totalUnitsInFile = response.totalUnitsInFile || 'N/A';
        state.response.header.totalUnitsSuccess = response.totalUnitsSuccess || '0';
        state.response.header.totalUnitsDuplicates = response.totalUnitsDuplicates || '0';
        state.response.header.totalUnitsDiscrepancy = response.totalUnitsDiscrepancy || '0';
        state.response.header.errorMessage = response.errorMessage;
        state.response.activeElement = [collapsibleElement];
      }
    },
    CLEAR_XML_RESPONSE: (state) => {
      state.response.details = [];
      state.response.header = {};
      state.response.activeElement = [];
    }
  },
  actions: {
    SetUploadXmlResponse: ({ commit }, response) => {
      commit('SET_XML_RESPONSE', response);
    },
    clearXmlResponse: ({ commit }) => {
      commit('CLEAR_XML_RESPONSE');
    }
  }
};

export default uploadXml;
